export default function(ua) {
    const normalized = (ua || '').toLowerCase();
    const agent = {
        Android: normalized.includes('android'),
        BlackBerry: normalized.includes('blackberry'),
        iOS: /iphone|ipad|ipod/i.test(normalized),
        OperaMini: normalized.includes('opera mini'),
        IEMobile: normalized.includes('iemobile'),
        Safari: normalized.includes('safari'),
        WebView: normalized.includes('wv'),
    };

    agent.isMobile = agent.Android || agent.BlackBerry || agent.iOS || agent.OperaMini || agent.IEMobile;

    return agent;
}
